import React, { useCallback, useMemo } from 'react';
import SimplePostItem from 'components/Items/SimplePostItem';
import WidgetCard from 'components/Cards/Widgets';
import { makeStyles } from '@material-ui/core';
import { useRecoilState } from 'recoil';
import { languageAtom } from 'atoms/language';
import { useAllPosts } from 'hooks/useAllPosts';

const MAX_POST_COUNT = 5;
const useStyles = makeStyles({
  wrapper: {
    margin: 0,
    padding: 0,
  },
});

interface Props {
  onClick?: () => void;
}
const RecentPostsCard = ({ onClick }: Props) => {
  const styles = useStyles();
  const [lang] = useRecoilState(languageAtom);
  const posts = useAllPosts(lang);

  const recent = useMemo(() => {
    return posts.splice(0, MAX_POST_COUNT);
  }, [posts]);

  const handleRecentPostClick = useCallback(() => {
    onClick?.();
  }, [onClick]);

  return (
    <WidgetCard title="최근 글">
      <ul className={styles.wrapper}>
        {recent.map((post, index) => (
          <SimplePostItem
            key={post.fields.path ?? index}
            thumbnail={post.frontmatter.thumbnail?.childImageSharp?.fixed?.src ?? ''}
            title={post.frontmatter.title ?? ''}
            date={post.frontmatter.date}
            path={post.fields.path ?? '/'}
            categories={post.frontmatter.categories}
            onClick={handleRecentPostClick}
          />
        ))}
      </ul>
    </WidgetCard>
  );
};

export default RecentPostsCard;
